<template>
  <nav>
    <div class="nav-wrapper green">
      <div class="container">
        <router-link to="/" class="brand-logo left">Employee Manager</router-link>
        <ul class="right">
          <li v-if="isLoggedIn">
            <span class="email black-text">
              {{currentUser}}
              <i class="material-icons left">person</i>
            </span>
          </li>
          <li v-if="isLoggedIn">
            <router-link to="/">
              Dashboard
              <i class="material-icons left">home</i>
            </router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/login">
              Login
              <i class="material-icons left">power_settings_new</i>
            </router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/register">
              Register
              <i class="material-icons left">person_add</i>
            </router-link>
          </li>
          <li v-if="isLoggedIn">
            <a v-on:click="logout" class="btn-small red">
              Logout
              <i class="material-icons left">exit_to_app</i>
            </a>
            <!-- <button v-on:click="logout" class="btn red">
              <i class="material-icons left">exit_to_app</i>
              Logout
            </button>-->
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import firebase from "firebase";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    }
  }
};
</script>

<style>
.email {
  padding-right: 10px;
}
</style>



